body, html {
  height: 100%;
}

* {
  box-sizing: border-box;
}

.nav{
  margin: 0;
  padding: 0;
  overflow: hidden;
  float: left;
}

.custom-select {
  display: block;
	font-size: 16px;
	font-family: 'Arial', sans-serif;
	font-weight: 400;
	color: #444;
	line-height: 1.3;
	padding: .4em 1.4em .3em .8em;
	width: 400px;
	max-width: 100%; 
	box-sizing: border-box;
	margin: 0;
	border: 1px solid #aaa;
	box-shadow: 0 1px 0 1px rgba(0,0,0,.03);
	border-radius: .3em;
	-moz-appearance: none;
	-webkit-appearance: none;
	appearance: none;
	background-color: #fff;
	background-image: url('data:image/svg+xml;charset=US-ASCII,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20width%3D%22292.4%22%20height%3D%22292.4%22%3E%3Cpath%20fill%3D%22%23007CB2%22%20d%3D%22M287%2069.4a17.6%2017.6%200%200%200-13-5.4H18.4c-5%200-9.3%201.8-12.9%205.4A17.6%2017.6%200%200%200%200%2082.2c0%205%201.8%209.3%205.4%2012.9l128%20127.9c3.6%203.6%207.8%205.4%2012.8%205.4s9.2-1.8%2012.8-5.4L287%2095c3.5-3.5%205.4-7.8%205.4-12.8%200-5-1.9-9.2-5.5-12.8z%22%2F%3E%3C%2Fsvg%3E'),
	  linear-gradient(to bottom, #ffffff 0%,#f7f7f7 100%);
	background-repeat: no-repeat, repeat;
	background-position: right .7em top 50%, 0 0;
	background-size: .65em auto, 100%;
   margin-top:20px;
   margin-bottom: 15px;
}

.custom-select::-ms-expand {
	display: none;
}

.custom-select:hover {
	border-color: #888;
}

.custom-select:focus {
	border-color: #aaa;
	box-shadow: 0 0 1px 3px rgba(59, 153, 252, .7);
	box-shadow: 0 0 0 3px -moz-mac-focusring;
	color: #222; 
	outline: none;
}

.custom-select{
	font-weight:normal;
}

.header-container {
  width: 100%;
  background-color: #fafafa;
  box-shadow: 0 2px 4px -2px rgba(17, 17, 17, 0.15);
  position: fixed;
  top: 0;
  z-index: 500;
}

.header {
  height: 50px;
  width: auto;
  max-width: 1400px;
  margin: 0 auto;
  overflow: visible;
  position: relative;
}

.header, .container-large {
  width: auto;
  margin-left: 20px;
  margin-right: 20px;
}

.header-logo {
  border-collapse: collapse;
  float: left;
}

.nav2-container{
  display: none;
  position: fixed;
  z-index: 25;
  overflow: hidden;
  background: #fcfcfc;
  border-radius: 10px;
  box-shadow: 0 50px 100px -20px rgba(50, 50, 93, 0.25), 0 30px 60px -30px rgba(0, 0, 0, 0.3), 0 -18px 60px -10px rgba(0, 0, 0, 0.025);
}

.nav > li {
  float: left;
  list-style: none;
  /* font-size: 18px; */
  transition: background 70ms ease-out;
  -webkit-transition: background 70ms ease-out;
  -moz-transition: background 70ms ease-out;
  -o-transition: background 70ms ease-out;
  position: relative;
}

.espacio{
  margin-top: 60px;
}

.columnas{
  column-count: 3;
  column-width: 5px;
  column-gap: 1px;
}